import { mapState, mapMutations } from 'vuex';
export default {
	computed:{
		...mapState(['hasLogin', 'forcedLogin', 'serverUrl', 'user'])
	},
	data() {
		return {
			// 验证码的个数（现在是6个）
			list: [
				{ val: '', fs: true }, // 初始发时使第一个输入框架得到焦点
				{ val: '', fs: false },
				{ val: '', fs: false },
				{ val: '', fs: false },
				{ val: '', fs: false }
			],
			show: false,
			entity:{}
		};
	},
	onLoad: function (option) {
		    const item = JSON.parse(decodeURIComponent(option.item));
			this.entity=item;
	},
	onReady() {
		// 显示弹出动画开关
		this.show = true;
		
		
	},

	methods: {
		// 当输入框的内容改变时的操作
		onInput(e, i) {
			// 输入完成时，跳到下一个输入框(得到焦点)
			if (i < this.list.length - 1 && e.target.value) {
				// 如果下一个输入框有值，则不跳
				if (!this.list[i + 1].val) this.onFocus(i + 1, true);
			}

			// 删除时，跳到上一个输入框
			if (i && !e.target.value) {
				// if(!this.list[i - 1].val)
				this.onFocus(i - 1, true);
			}
		},

		// 得到焦点 激活下边框样式
		onFocus(i, t) {
			// this.list[i].val = '';	// 清除输入框中的内容
			this.list[i].fs = true;
		},

		// 失去焦点 去除下边框样式
		onBlur(i) {
			this.list[i].fs = false;
		},

		// 提示验证码方法
		onSubmit() {
			let code = '';
			var that=this;
			for (let i = 0, len = this.list.length; i < len; i++) {
				// 获取用户输入的验证码
				code += this.list[i].val;
			}
			if (code && 5 == code.length) {
				
				        console.log(that.entity)
						that.saveEnitity(that.entity,code); 
					
				
			
			} else {
				uni.showToast({
					title: '请正确输入验证码！',
					image: '../../static/img/alert-3.png',
					duration: 3000
				});
			}
		},
		saveEnitity(user, yan) {
			
			uni.showLoading({
			    title: '更新中'
			});
			uni.request({
				url: this.serverUrl + '/user/update/' + user.phone + '/' + yan, //仅为示例，并非真实接口地址。
				method: 'POST',
				data: user,
				header: {
					'content-type': 'application/json'
					//自定义请求头信息
				},
				success: res => {
					
					 uni.hideLoading();
					if (res.data.status) {
						uni.showToast({
							title: '更新成功：',
							image: '../../static/img/alert-2.png',
							duration: 3000
						});
						uni.switchTab({
						url:'../user/user'
						})
						
					} else {
						console.log(res.data.message)
						uni.showToast({
							title: '更新失败！',
							image: '../../static/img/alert-3.png',
							duration: 3000
						});
					}
				},
				fail: err => {
					 uni.hideLoading();
					uni.showToast({
						title: '更新失败！',
						image: '../../static/img/alert-3.png',
						duration: 3000
					});
				}
			});
		}
	}
};
